



































































































































































.order-edit-modal--address-select {
    label {
        border-radius: 0.375rem;
        border: 1px solid transparent;
        display: block;
        margin: -1px;
        width: 25rem;
    }

    label.selected {
        border-width: 2px;
        border-color: #2563eb;
        margin: -2px;
    }

    .body > div + div {
        margin-top: 14px;
    }

    .address-card-header-actions {
        display: none;
    }
}
