.line-item {
    transition: background-color 0.5s ease;
    position: relative;

    &-bg {
        transition: background-color 0.5s ease;
        height: 100%;
        top: 0;
        left: -24px;
        right: -24px;

        &.highlight-line-item {
            background: #FFFFF0;
        }
    }

    &-section {
        width: 33.3333%;
    }

    &-buttons::after {
        content: '.';
        display: block;
        height: 0;
        clear: both;
        visibility: hidden;
    }

    label {
        @include padding-right(10px);
        max-width: 100%;
    }
}
