























































.order-address-display {
  position: relative;

  .zilch {
      padding-bottom: 5rem;
      padding-top: 5rem;
  }

  .address-card .address-card-header-actions {
      display: none;
  }

  &-buttons {
    position: absolute;
    top: 1rem;
    right: 1rem;

    *:not(:last-child) {
      margin-right: 4px;
    }
  }
}
